import {createLevelsFromArray} from "core/types/logic"
import * as _ from 'lodash'

export type Modification = {
  color?: boolean
  onlyOne?: boolean
  odd?: boolean
  reverse?: boolean
}

export interface Level {
  smallCount: boolean
  isNumber: boolean
  modification: Modification
}

function level(smallCount: boolean, modification: Modification): Level[] {
  const types = modification.odd ? [true] : [true, false]
  return types.map(isNumber => ({smallCount: smallCount && isNumber, isNumber, modification}))
}

export const levels = createLevelsFromArray(_.flatten([
  level(true, {}),
  level(true, {odd: true}),
  level(true, {onlyOne: true}),
  level(false, {odd: true}),
  level(false, {onlyOne: true}),
  level(false, {color: true}),
  level(false, {color: true, odd: true}),
  level(false, {reverse: true}),
]))

export const tutorialLevels = level(true, {onlyOne: true})



