import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Signals} from "core/ui/animations/signals"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {TextPanelComponent} from "core/ui/components/games/gameUI/textPanelComponent"
import {OnInit} from "core/ui/components/onInit"
import {Images} from "games/speedread/findletter/assets"
import {IntroComponent} from "games/speedread/findletter/components/introComponent"
import {QuestionComponent} from 'games/speedread/findletter/components/questionComponent'
import {Task} from '../logic'

const durations = {
  introHideDelay: 300
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    taskDescSize: {width: 800, height: 120}
  })

  biPositions = Sizes.bi({
    taskDesc: {x: 1020, y: 195},
    question: {x: 1020, y: 720}
  }, {
    taskDesc: {x: 1020, y: 125},
    question: {x: 1020, y: 720}
  })

  bg = this.addImage(Images.bg)
  question = this.add(this.createComponent(QuestionComponent)).anchorCenter().setBiPos(this.biPositions.question)

  private taskDesc: TextPanelComponent

  onInit(): void {

    this.context.uiService.answerStatus.hidePartial = true
    this.statusOptions.waitCompleteBeforeNewTask = true
    this.startIntro()

    this.taskDesc = this.add(
      new TextPanelComponent({
        size: this.positions.taskDescSize
      })
    )
      .anchorCenter()
      .setBiPos(this.biPositions.taskDesc)

    this.taskDesc.visible = false
    this.onIntroCompleteOrMemorize.addOnce(() => {
      this.taskDesc.visible = true
    })
  }

  introAnimation(): { start: () => void; onComplete: Phaser.Signal } {
    const intro = this.add(this.createComponent(IntroComponent))
    const tween = intro.playTween()

    // tween.onComplete.addOnce(() => {
    //   intro.destroy()
    // })

    return {
      start: () => {
        // this.playSound("bg", true)
        tween.start()
      },
      // onComplete: tween.onComplete,
      onComplete: Signals.bind(tween.onComplete, () => {
        const signal = CommonAnimations.show(intro, false, undefined, durations.introHideDelay).onComplete
        signal.addOnce(() => {
          intro.destroy()
        })
        return signal
      })
    }
  }

  onNewTask(task: Task) {
    const isNumber = task.isNumber ? "_number" : "_letter"
    const odd = task.modification.odd ? "_odd" : ""
    const color = task.modification.color ? "_color" : ""
    const onlyOne = task.modification.onlyOne ? "_onlyone" : ""
    const reverse = task.modification.reverse ? "_reverse" : ""

    this.taskDesc.setProps(this.translate('task' + isNumber + odd + color + reverse + onlyOne)
      + (task.oneSymbol ? " " + task.oneSymbol : ""))
  }

}




