  onAnswer(answer: ILastAnswer): void {
    if(AnswerStatus.isCorrect(answer.status)){
      this.correct.visible = true
      const delay = answer.status == AnswerStatus.CorrectPartial
        ? durations.correctPartial
        : durations.correct
      this.delay(delay).addOnce(() => this.correct.visible = false)
    }
    // this.buttons.find(x => x.tag == answer.answerId)!.visible = false
  }

  createNewTask(task: Task) {
    this.removeAll(true)

    const listIndex = this.rng.sample(task.isNumber ? this.numberLists : this.letterLists)

    this.correct = this.addImage(Images.correct)
      .setPos(this.positions.book).fluentUpdate(x => x.visible = false)
    this.addImage(Images.book)
      .setPos(this.positions.book)
    this.group = this.add(new Control())

    // this.paper = this.addImage(Images.paper)
    //   .setPos(this.positions.paper)

    this.group.add(this.makeImage(Images.list(listIndex)))
      .setPos(this.positions.list)

    // this.animationPageTurn() // запускаем анимацию страницы

    const fixedLetters = _.filter(this.positions.fields[listIndex], letter => {
      return letter[2] !== "50"
    })

    const floatLetters = _.filter(this.positions.fields[listIndex], letter => {
      return letter[2] === "50"
    })

    // const sortedData = fixedLetters.concat(this.rng.derangement(floatLetters)).slice(0, 48)

    const sortedData = fixedLetters.concat(this.rng.derangement(floatLetters))
    const maxCount = sortedData.length
    const fillLettersLength = maxCount - task.letters.length
    const allSymbols = task.isNumber ? getAllSymbols(false) : _.range(10).map(x => x.toString())
    const fillLetters = _.range(fillLettersLength).map(() => this.rng.sample(allSymbols))
      .map(letter => ({id: "", letter, isRed: false}))

    // Вставляем буквы
    this.buttons = task.letters.concat(fillLetters).map((letter, index) => {

      const data = sortedData[index]
      const btn = new Btn(letter.letter, () => {
      }, Fonts.letter(parseInt(data[2]),
        letter.isRed, this.rng.bool())).anchorCenter()
      // btn.showDebug()

      btn.updatePos(data[0] + btn.width / 2, data[1] + btn.height / 2)
      // btn.updatePos(data[0], data[1])

      return this.addAnswerBehavior( // отвечает за отображение правильных ответов
        this.group.add(btn),
        letter.id,
        {
          dark: true
        }
      )
        .fluentUpdate(btn => btn.tag = letter)
    })

    // для создания анимации сначала добавляем картинку, и выставляем ей координаты
    // this.addImage(Images.paper)
    // .setPos(this.positions.page)
    // .anchorCenter()
    // .setPos(this.center())

    this.updatePivot()
    this.answersAreReady()
    CommonAnimations.show(this.group, true).onComplete.addOnce(() => this.disableInput(false))
    this.updateScale(this.current(biValues.data))
  }

  onNewTask(task: Task): void {
    this.disableInput(true)
    if (!this.isFirstTask) {
      CommonAnimations.show(this.group, false).onComplete.addOnce(() => this.createNewTask(task))
    } else {
      this.createNewTask(task)
    }
    this.playSound(Sounds.paper)
  }

