import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"

const durations = {
  arm: 1000
}

export class IntroComponent extends GameComponent {
  positions = Sizes.create({
    from: {x: 1155, y: 418},
    to: {x: 1155, y: 860}
  })

  intro = this.addImage("intro")
  arm = this.addImage("arm")

  onInit() {
  }

  playTween() {
    this.arm.setPos(this.positions.from)

    return Tweens.start({
      target: this.arm,
      delay: 300,
      to: this.positions.to,
      duration: durations.arm,
      onComplete: () => {
      }
    })

  }

}




