import {CoreColors, CoreFonts} from "core/assets/fonts"
import {ImagesMetaData} from "core/types"

export module Sounds {
  export const bg = "bg"
  export const paper = "paper"
  export const keys: string[] = [bg, paper]
}

export module Translations {
  // export const vowel = "task_vowel"
  // export const consonant = "task_consonant"
  // export const red = "task_red"
  // export const black = "task_black"
  // export const black = "task_black"
  // export const black = "task_black"
}

export module Fonts {
  export const letter = (sizeIndex: number, isRed: boolean, bold: boolean) => ({
    fontSize: sizeIndex,
    font: bold ? CoreFonts.MontserratBold : CoreFonts.MontserratLight,
    fill: isRed ? CoreColors.red : CoreColors.black,
    align: "center",
    wordWrap: true
  })
}

export module Images {
  export const bg = "bg"
  export const arm = "arm"
  export const book = "book"
  export const correct = "correct"
  export const book_open = "book_open"
  export const list = (index: number) => "list_" + index
  export const page = "page"
  export const paper = "paper"
  export const intro = "intro"
  export const data: ImagesMetaData = {
    sprites: []
  }

}




