import {GameLogic} from "core/logic"
import {Sys} from "core/logic/sys"
import {Arrays, RandomGenerator} from "core/utils"
import {Numbers} from "core/utils/numbers"
import * as _ from 'lodash'
import {Level, Modification, tutorialLevels} from "./levels"

const allLetters = "абвгдеёжзийклмнопрстуфхцчшщъыьэюя".split("")
// const allLetters = "щщщщщщщщщщщщщщщщщщщщщщщщщщщщщщщщщ".split("")
// const allLetters = "ууууууууууууууууууууууууууууууууу".split("")
// const allLetters = "ббббббббббббббббббббббббббббббббб".split("")
// const allLetters = "ффффффффффффффффффффффффффффффффф".split("")
const allNumbers = _.range(100).map(x => (x + 1).toString())

// const allNumbers = _.range(100).map(x => "34")

export function getAllSymbols(isNumber: boolean) {
  return isNumber ? allNumbers : allLetters
}

type Symbol = {
  id: string
  letter: string
  isRed: boolean
}

function newSymbol(letter: string, isRed: boolean): Symbol {
  const id = `${letter}_${isRed ? "red" : "black"}`
  return {id, letter, isRed}
}

export type Target = {
  isVowel?: Boolean
  isRed?: boolean
}

export type Task = {
  letters: Symbol[]
  oneSymbol?: string
  isNumber: boolean
  answers: { seq: string[] }
  modification: Modification
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()

  createLetters(isNumber: boolean, count: number, isRed: boolean) {
    const symbols = getAllSymbols(isNumber)
    return _.take(symbols, count).map(x => newSymbol(x, isRed))
  }

  createTaskSymbols(level: Level) {
    const withColor = level.modification.color
    const isNumber = level.isNumber
    const maxCount = level.smallCount ? 30 : 48
    if (withColor) {
      const count = maxCount / 2
      const parts = [
        this.createLetters(isNumber, count, false),
        this.createLetters(isNumber, count, true)
      ]
      return _.flatMap(Arrays.zip(parts[0], parts[1]), x => x)
    }
    return this.createLetters(isNumber, maxCount, false)
  }

  createAnswers(modification: Modification, symbols: Symbol[]) {

    if (modification.onlyOne) {
      const answer = this.rng.sample(symbols)
      return [answer]
    }
    if (modification.odd && modification.color) {
      return _.flatten(_.chunk(symbols, 2).filter((x, i) => Numbers.odd(i)))
    }

    if (modification.odd) {
      return symbols.filter((x, i) => Numbers.odd(i))
    }
    if (modification.reverse) {
      return _.reverse(symbols)
    }

    return symbols

  }

  nextTask(lvl: Level) {
    if (this.isTutorial && this.taskIndex >= tutorialLevels.length) {
      return undefined
    }
    const level = this.isTutorial ? tutorialLevels[this.taskIndex] : lvl
    const modification = level.modification
    const taskSymbols = this.createTaskSymbols(level)
    const letters = this.rng.shuffle(taskSymbols)
    const answerSymbols = this.createAnswers(modification, taskSymbols)
    const answers = {seq: answerSymbols.map(x => x.id)}
    const oneSymbol = modification.onlyOne ? answerSymbols[0].letter : undefined
    const isNumber = level.isNumber

    const task: Task = {
      isNumber,
      letters,
      oneSymbol,
      modification,
      answers
    }
    if (Sys.showLog) {
      console.log("task", task)
    }
    return task
  }

}





